@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Montserrat', sans-serif;
  line-height: 1.4em;
}

a {
  color: #809c8e;
  text-decoration: none;
}

h1, 
h2,
h3,
h4, 
p {
  padding: 5px;
  margin: 5px;
}

